.event-list {
  table-layout: auto;
  -webkit-user-select: none;
  user-select: none;
  position: absolute;
}

.event-list td, .event-list th {
  padding: 3px 10px;
}

.event-list thead tr {
  color: #fff;
  width: 100%;
  text-align: left;
  background: #000;
  margin: 3px 10px;
  padding-top: 3px;
  padding-bottom: 3px;
}

.event-list thead tr .id:before {
  content: "Id";
}

.event-list thead tr .camera:before {
  content: "Camera";
}

.event-list thead tr .start:before {
  content: "Start";
}

.event-list thead tr .end:before {
  content: "End";
}

.event-list thead tr .duration:before {
  content: "Duration";
}

.event-list .delete .danger.btn {
  transform: initial;
  position: relative;
}

.event-list thead th:last-child {
  cursor: pointer;
}

.event-list th:last-child:hover .danger.btn, .event-list td:last-child:hover .danger.btn {
  display: none;
}

.event-list th:last-child:hover .danger.btn.hover, .event-list td:last-child:hover .danger.btn.hover {
  display: initial;
}

.event-list .last-of-day:not(:last-child) td {
  border-bottom: 1px dashed gray;
}

.event-list tbody tr {
  cursor: pointer;
}

.event-list tbody tr:hover {
  background: #877abf;
}

.event-list tbody tr:hover td:last-child {
  background: #fff;
}

.event-list tbody tr:active {
  background: #9d8ed9;
}

.event-list tbody tr > :nth-child(1) {
  font-size: .7em;
}

@media screen and (max-width: 650px) {
  .event-list tr > :nth-child(4) {
    display: none;
  }

  .event-list tr:first-child .camera:before {
    content: "Cam";
  }
}

@media screen and (max-width: 465px) {
  .event-list tr :nth-child(5) {
    display: none;
  }

  .event-list tr:first-child .camera:before {
    content: "Cam";
  }
}

